body {
  background: url("./images/NASA_bgc.jpg");
}

* {
  box-sizing: border-box;
}

.app {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  color: #fff;

  &__logo {
    position: absolute;
    top: 0;
    height: 100px;
    width: 100px;
    background: url('./images/favicon-192.png') no-repeat;
    background-size: contain;
    cursor: pointer;
    // animation: rotator 5s linear infinite;
  }

  &__title {
    margin-left: 100px;
    margin-bottom: 50px;
    font-weight: 700;
    color: #154595;
  }

  &__selects {
    padding-left: 10px;
    height: 100px;
    width: 250px;
  }
}

@keyframes rotator {
  0% {
    transform: rotateY(360deg);
  }
}
